import {
  ApolloClient,
  ApolloProvider,
  InMemoryCache,
  gql,
} from '@apollo/client';
import React from 'react';
import { BrowserRouter } from 'react-router-dom';

import Routes from './routes';
import GlobalStyle from './styles/global';
// import { Container } from './styles';

export const client = new ApolloClient({
  uri: 'https://mvrp.herokuapp.com/api/cars',
  // uri: 'https://600a3dc8778d1a00177937e0.mockapi.io/getSongs',
  cache: new InMemoryCache(),
});

const App: React.FC = () => {
  return (
    <ApolloProvider client={client}>
      <BrowserRouter>
        <Routes />
        <GlobalStyle />
      </BrowserRouter>
    </ApolloProvider>
  );
};

export default App;
